@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  background: transparent;
  height: 10vh;
}
nav{
  display: flex;
  position: relative;
  z-index: 10;
}
.navList{
  position: absolute;
  right: 20px;
  top: 20px;
}
.navLink{
  text-decoration: none;
  font-size: 20px;
  margin: 0px 20px;
  font-weight: bolder;
  color: #D4F7D4;
}
.navLink:hover{
  text-decoration: none;
  color: #FD3A0F;;
}
  /*Open and Close Nav Menu*/
.topnav.responsive { position: relative; }
.topnav.responsive .menu {
  position: absolute;
  top: 20px;
  right: 20px;
}
.topnav.responsive .navLink {
  text-align: center;
  padding: 20px;
  font-size: 25px;
  display: block;
  margin: auto;
}
.topnav.responsive .navList{ 
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding-top: 10px;
  background: linear-gradient(to bottom, rgba(92, 77, 66, 0.8) 0%, rgba(92, 77, 66, 0.8) 100%), url(/static/media/home.7e2de8f6.jpg);
  background-size: cover;
  background-position: center;
  z-index: 10;
  height: 50vh;
  display: flex;
  flex-direction: column;
}

/* 3. Hamburger menu in mobile responsive*/
header button{
  height: 25px;
  width: 25px;
  background: none;
  border: none;
  }
button:focus{ outline: none; }
.menu-line{
  display: block;
  position: relative;
}
.menu-line,
.menu-line::before,
.menu-line::after{
  width: 25px;
  height: 3px;
  background-color: white;
  transition: transform 250ms ease-in-out;
}
.menu-line::before,
.menu-line::after{
  content: "";
  position: absolute;
  left: 0;
}
.menu-line::before{ bottom: 6px; }
.menu-line::after{ top: 6px; }

/* 4. Hamburger menu animation (menu to x)*/
.topnav.responsive .menu{ transform: rotate(45deg); }
.topnav.responsive .menu-line::before{ opacity: 0; }
.topnav.responsive .menu-line::after{
  transform: rotate(90deg) translate(-6px);
}

@media(min-width: 1050px){
  .menu{ display: none; }
  header{ padding: 5px; }
  .topnav .navLink{
      color: #D4F7D4;
      text-decoration: none;
      font-size: 20px;
      margin-left: 20px;
      margin-right: 20px;    
  }
  .topnav #navList{
      text-align: right;
      margin-right: 80px;
      padding: 8px;
  }

  /* underline on hover */
  .navLink{ position: relative; }
  .navLink:hover::after {
      content: "";
      position: absolute;
      bottom: -20px;
      left: 0px;
      height: 4px;
      width: 100%;
      background-color: #FD3A0F;
  }
  #portfolioNav{ color: #FD3A0F; }
  #portfolioNav:hover{ color: #D4F7D4; }
}
@media(max-width: 1050px){
  .topnav .navLink { display: none; }
}
