.projectDescription{
    position:absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    visibility: hidden;
    opacity: 0;
    background-color:  rgba(212, 247, 212, 0.85);
    transition: opacity 0.25s ease ;
    color: #FD3A0F;
    font-size: 14px;
    padding: 10px 5px;
    margin: auto;
}
.imageContainer:hover .projectDescription{
    opacity: 1;
    visibility: visible;
}
.descriptionLink{
    position: absolute;
    bottom: 10px;
    right: 5px;
    left: 5px;
}
.projectLink, .githubLink{
